.app .post {
  margin: 30px 20px; }
  @media screen and (max-width: 550px) {
    .app .post {
      margin: 15px 10px; } }

.header {
  position: relative;
  padding: 15px;
  text-align: center;
  background-color: #67aef6; }
  .header h1 {
    color: #fff;
    font-size: 30px;
    font-weight: bold; }
  .header .nav, .header .write-btn {
    position: absolute;
    top: 0; }
  .header .nav {
    left: 20px; }
    .header .nav li {
      display: inline-block;
      vertical-align: middle;
      height: 75px;
      box-sizing: border-box; }
  .header .write-btn {
    top: 25px;
    right: 20px; }
    .header .write-btn p {
      font-size: 20px; }
      .header .write-btn p span {
        padding-top: 5px; }
  @media screen and (max-width: 550px) {
    .header {
      padding: 15px 0; }
      .header h1 {
        font-size: 20px; }
      .header .nav {
        left: 10px; }
      .header .write-btn {
        top: 20px;
        right: 10px; } }

.btn1 {
  color: #fff;
  font-size: 14px;
  cursor: pointer; }
  .btn1 p {
    font-size: 30px; }
    .btn1 p span {
      display: inline-block;
      vertical-align: text-top;
      padding: 12px 5px 0;
      color: #fff;
      font-size: 14px; }
  .btn1.icon-btn {
    text-align: center;
    padding: 15px 20px; }
    .btn1.icon-btn p {
      padding-right: 5px; }
      .btn1.icon-btn p span {
        font-size: 16px;
        opacity: 0.5; }
  .btn1.on {
    border-bottom: 4px solid #fff;
    cursor: default; }
    .btn1.on p {
      opacity: 1; }
  @media screen and (max-width: 550px) {
    .btn1 p {
      font-size: 20px; }
      .btn1 p span {
        display: none; }
    .btn1.icon-btn {
      padding: 21px 10px 9px; } }

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10; }
  .modal .dim-bg {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); }
  .modal .write-box {
    position: absolute;
    left: 50%;
    top: 50%;
    margin: -251px 0 0 -251px;
    padding: 20px;
    width: 500px;
    height: 300px;
    border-radius: 4px;
    background-color: #fff;
    box-sizing: border-box; }
    .modal .write-box h2 {
      font-size: 20px;
      font-weight: 600;
      color: #333;
      padding-bottom: 15px; }
    .modal .write-box textarea {
      width: 100%;
      height: 170px;
      padding: 5px 10px;
      resize: none;
      overflow-y: auto;
      border-radius: 2px;
      border: 1px solid #ddd;
      box-sizing: border-box; }
    .modal .write-box .btn1 {
      position: absolute;
      right: 25px;
      bottom: 25px;
      color: #333; }
      .modal .write-box .btn1 p {
        font-size: 14px; }
      .modal .write-box .btn1 span {
        padding: 1px 3px 0;
        color: #333; }
    .modal .write-box .close {
      position: absolute;
      right: 20px;
      top: 10px;
      font-size: 25px; }
  @media screen and (max-width: 550px) {
    .modal .write-box {
      width: 90%;
      margin: -200px 0 0;
      top: 50%;
      left: 5%; }
      .modal .write-box .btn1 p {
        font-size: 20px; }
        .modal .write-box .btn1 p span {
          display: inline-block;
          padding: 4px 3px 0;
          font-size: 15px; } }

.post-item {
  position: relative;
  margin-top: 15px;
  padding: 20px;
  background-color: #fff; }
  .post-item span {
    display: block;
    padding-bottom: 10px;
    color: #999; }
  .post-item p {
    font-size: 14px;
    line-height: 1.5;
    white-space: pre-line;
    -ms-word-wrap: break-word;
    word-wrap: break-word;
    -ms-word-break: break-all;
    word-break: break-all; }
  .post-item .delete {
    position: absolute;
    right: 23px;
    top: 10px;
    color: #999;
    font-size: 20px; }
  @media screen and (max-width: 550px) {
    .post-item {
      margin-top: 10px;
      padding: 15px; } }

.no-content {
  padding: 60px 0;
  text-align: center;
  font-size: 15px;
  background-color: #fff;
  color: #999; }
  .no-content span {
    display: block;
    font-size: 30px;
    color: #999; }

